  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: #424242;
}

.header {
  min-height: 80px;
  transition: min-height 0.3s;
  padding: 10px;
  width: 100%;
  text-align: center;
  position: sticky;
  top: 0;

  background-color: #363737;
  display: flex;
  align-items: center;
}

.header a {
  text-decoration: none;
  padding: 0.5rem 1.5rem;
  border-radius: 99px;
  font-size: 20px;
  font-weight: 500;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  display: flex;
  margin-right: 1%;
}
.header button {
  padding: 0.5rem 1.5rem;
  border-radius: 99px;
  font-size: 20px;

  cursor: pointer;
  transition: all 0.2s ease-in-out;
  display: flex;
}

.header_title a:hover {
  border: 3px solid #f2f2f2;
  background-color: transparent;

  color: white;
}

.nav {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.nav li {
  list-style-type: none;
}

.nav .nav_item {
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
  line-height: 25px;
  text-transform: uppercase;
}

.nav li a {
  padding: 1rem 1.5rem;
  border-radius: 99px;
  width: 100%;
  text-decoration: none;
  font-weight: 500;

  transition: all 0.2s ease-in-out;
}

.nav li:hover a {
  border: 3px solid #f2f2f2;
  background-color: transparent;

  color: white;
}

.hero {
  min-height: 380px;
}

.hero img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  object-position: center center;
}

.hero-inner {
  color: #fff;
  width: 100%;
  height: 50%;
  padding: 0 16px;
  margin-top: 50px;
  position: absolute;
  top: 20%;
  z-index: 1;
}

.hero-title {
  font-size: 48px;
  font-weight: 700;
  text-align: center;
  font-family: cursive;
}

.hero-tagline {
  font-size: 24px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}

main {
  width: 100%;
  margin: 0 auto;
}

.content {
  padding: 32px;
}

.boarding {
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 60px;
}

.headline_figure {
  width: 100%;
}

.headline_figure img {
  width: 100%;
  max-height: 90%;
}

.headline_figure figcaption {
  text-align: center;
  color: #666666;
  font-size: 13px;
  font-weight: 400;
  margin-top: 8px;
}

.headline_content {
  width: 100%;
  padding: 16px 0;
}

.headline_title {
  font-size: 24px;
  font-weight: 500;
}

.headline_description {
  font-size: 12px;
  margin-top: 12px;
}

.headline_button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  margin-top: 24px;
  cursor: pointer;
  padding: 1rem 1.5rem;
  color: white;
  border: none;
  border-radius: 5px;
  background-color: #363737;
  transition: opacity 0.3s;
}

.headline_button:hover {
  opacity: 0.8;
}

.heading {
  width: 100%;
  margin: 20px auto;
  text-align: center;
}

.heading_title {
  font-size: 24px;
  font-weight: 500;
}

.restaurant-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}

.restaurant-item_header {
  position: relative;
}

.restaurant-item .restaurant-item_header .restaurant-item_thumbnail {
  width: 100%;
  height: 250px;
}

.restaurant-item .restaurant-item_header .restaurant-item_rating {
  position: absolute;
  padding: 10px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.restaurant-item .restaurant-item_content {
  padding: 16px;
  background-color: #f7f7f7;
}

.restaurant-item .restaurant-item_content h3 a {
  padding: 1rem 1rem;
  color: #363737;
  text-decoration: none;
}

.restaurant-item .restaurant-item_content h3 a:hover {
  color: #db0000;
}

.restaurant-item .restaurant-item_content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.restaurant_thumbnail {
  width: 100%;
}

.restaurant_thumbnail img {
  width: 100%;
}

.restaurant_info {
  padding: 16px;
  text-align: left;
}

footer {
  padding: 2rem;
  color: #fff;
  position: absolute;
  width: 100%;
  background-color: #363737;
  text-align: center;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
  z-index: 1000;
}

.input_section {
  background-color: #f7f7f7;
  align-items: center;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 20px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 30px;
}

.input_section > form > button {
  background-color: #363737;
  color: white;
  border: 0;
  border-radius: 5px;
  display: block;
  width: 100%;
  padding: 1rem 1rem;
  cursor: pointer;
  margin-top: 10px;
}

.input_section > form > .input > input {
  display: block;
  width: 100%;
  padding: 1rem 1rem;
  border-radius: 5px;
  margin-bottom: 20px;
}

.input_section > form > .input > textarea {
  display: block;
  width: 100%;
  height: 100px;
  padding: 8px;
  border-radius: 5px;
}

.input_section > form > .input > label {
  font-size: 1rem;
  margin-bottom: 10px;
  color: #363737;
  font-weight: bold;
}

.list {
  background-color: #f7f7f7;
  align-items: center;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 10px;
  padding: 30px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 30px;
}
